* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	background-color: #eff3f7;
	height: 100%;
}

html,
body,
#root {
	height: 100%;
	width: 100%;
}

@media (max-width: 600px) {
	.questions {
		display: flex;
		padding: 50px;
		padding-top: 30px;
		width: 100%;
	}

	.questions .qAndA {
		width: 100%;
		height: 7vh;
	}

	form {
		display: flex;
		justify-content: center;
	}

	form .button {
		margin: 10px;
		width: 30%;
		height: 20px;
	}
}
